import React, { Component } from 'react';
import Container from './react-smooth-dnd/src/Container';
import Draggable from './react-smooth-dnd/src/Draggable';
import { applyDrag, generateItems } from './utils';

const stopEvent = event => {
  event.preventDefault();
  event.stopPropagation();
}

export default class SortableList extends Component {
    constructor() {
      super();
      this.state = {
        style: null,
        items: generateItems(50, (index) => {
          return {
            id: index,
            data: 'Draggable' + index
          };
        })
      };
    }
    preventScroll = e => {
      console.log("preventScroll")
      this.setState({style: {overflow: 'hidden'}});
    }
    allowScroll = e => {
      console.log("allowScroll")
      this.setState({style: {overflow: 'auto'}});
    }
    setContainerRef = el => {
      this.containerRef = el;
      el.addEventListener("touchmove", this.preventScroll );

    }
    touchStart = e => {
      console.log(e);
    }

    componentDidMount = () => this.containerRef.addEventListener("contextmenu", stopEvent)
    render() {
      return (
        <div>
          <div className="simple-page-scroller" style={this.state.style} ref={this.setContainerRef} onScroll={this.onTouchStart}>
            <Container onDragStart={this.preventScroll} onDragEnd={this.allowScroll} onDrop={e => this.setState({ items: applyDrag(this.state.items, e) })}>
              {this.state.items.map(p => {
                return (
                  <Draggable key={p.id}>
                    <div className="draggable-item">
                      {p.data}
                    </div>
                  </Draggable>
                );
              })}
            </Container>
          </div>
        </div>
      );
    }
  }